const PI = Math.PI; // PI
const X_PI = PI * 3000.0 / 180.0;
const a = 6378245.0; // åŒ—äº¬54åæ ‡ç³»é•¿åŠè½´a=6378245m
const ee = 0.00669342162296594323;

/**
 * è½¬æ¢çº¬åº¦
 * @param lng
 * @param lat
 * @returns {number}
 */
const transformlat = (lng, lat) => {
  let ret = -100.0 + 2.0 * lng + 3.0 * lat + 0.2 * lat * lat + 0.1 * lng * lat + 0.2 * Math.sqrt(Math.abs(lng));
  ret += (20.0 * Math.sin(6.0 * lng * PI) + 20.0 * Math.sin(2.0 * lng * PI)) * 2.0 / 3.0;
  ret += (20.0 * Math.sin(lat * PI) + 40.0 * Math.sin(lat / 3.0 * PI)) * 2.0 / 3.0;
  ret += (160.0 * Math.sin(lat / 12.0 * PI) + 320 * Math.sin(lat * PI / 30.0)) * 2.0 / 3.0;
  return ret;
};

/**
 * è½¬æ¢ç»åº¦
 * @param lng
 * @param lat
 * @returns {number}
 */
const transformlng = (lng, lat) => {
  let ret = 300.0 + lng + 2.0 * lat + 0.1 * lng * lng + 0.1 * lng * lat + 0.1 * Math.sqrt(Math.abs(lng));
  ret += (20.0 * Math.sin(6.0 * lng * PI) + 20.0 * Math.sin(2.0 * lng * PI)) * 2.0 / 3.0;
  ret += (20.0 * Math.sin(lng * PI) + 40.0 * Math.sin(lng / 3.0 * PI)) * 2.0 / 3.0;
  ret += (150.0 * Math.sin(lng / 12.0 * PI) + 300.0 * Math.sin(lng / 30.0 * PI)) * 2.0 / 3.0;
  return ret;
};

/**
 * åˆ¤æ–­åæ ‡æ˜¯å¦åœ¨å›½å†…ï¼ˆå›½å¤–åæ ‡ä¸éœ€è½¬æ¢ï¼‰
 * @param lng
 * @param lat
 * @returns {boolean}
 */
const outOfChina = (lng, lat) => {
  // çº¬åº¦3.86~53.55,ç»åº¦73.66~135.05
  return !(lng > 73.66 && lng < 135.05 && lat > 3.86 && lat < 53.55);
};

/**
 * å›½æµ‹å±€J02ï¼ˆç«æ˜Ÿåæ ‡ç³» (GCJ-02)ï¼‰åæ ‡è½¬WGS84
 * @param lng
 * @param lat
 * @returns {[*,*]}
 */
const gcj02towgs84 = (lng, lat) => {
  if (outOfChina(lng, lat)) {
    return [lng, lat];
  } else {
    let dlat = transformlat(lng - 105.0, lat - 35.0);
    let dlng = transformlng(lng - 105.0, lat - 35.0);
    let radlat = lat / 180.0 * PI;
    let magic = Math.sin(radlat);
    magic = 1 - ee * magic * magic;
    let sqrtmagic = Math.sqrt(magic);
    dlat = (dlat * 180.0) / ((a * (1 - ee)) / (magic * sqrtmagic) * PI);
    dlng = (dlng * 180.0) / (a / sqrtmagic * Math.cos(radlat) * PI);
    let mglat = lat + dlat;
    let mglng = lng + dlng;
    return [lng * 2 - mglng, lat * 2 - mglat];
  }
};

/**
 * WGS84è½¬å›½æµ‹å±€J02ï¼ˆç«æ˜Ÿåæ ‡ç³» (GCJ-02)ï¼‰
 * @param lng
 * @param lat
 * @returns {[*,*]}
 */
const wgs84togcj02 = (lng, lat) => {
  if (outOfChina(lng, lat)) {
    return [lng, lat];
  } else {
    let dlat = transformlat(lng - 105.0, lat - 35.0);
    let dlng = transformlng(lng - 105.0, lat - 35.0);
    let radlat = lat / 180.0 * PI;
    let magic = Math.sin(radlat);
    magic = 1 - ee * magic * magic;
    let sqrtmagic = Math.sqrt(magic);
    dlat = (dlat * 180.0) / ((a * (1 - ee)) / (magic * sqrtmagic) * PI);
    dlng = (dlng * 180.0) / (a / sqrtmagic * Math.cos(radlat) * PI);
    let mglat = lat + dlat;
    let mglng = lng + dlng;
    return [mglng, mglat];
  }
};

/**
 * å›½æµ‹å±€J02ï¼ˆç«æ˜Ÿåæ ‡ç³» (GCJ-02)ï¼‰è½¬ç™¾åº¦åæ ‡ç³»
 * @param lng
 * @param lat
 * @returns {[*,*]}
 */
const gcj02tobd = (lng, lat) => {
  let z = Math.sqrt(lng * lng + lat * lat) + 0.00002 * Math.sin(lat * X_PI);
  let theta = Math.atan2(lat, lng) + 0.000003 * Math.cos(lng * X_PI);
  let bdLng = z * Math.cos(theta) + 0.0065;
  let bdLat = z * Math.sin(theta) + 0.006;
  return [bdLng, bdLat];
};

/**
 * ç™¾åº¦åæ ‡ç³»è½¬å›½æµ‹å±€J02ï¼ˆç«æ˜Ÿåæ ‡ç³» (GCJ-02)ï¼‰
 * @param lon
 * @param lat
 * @returns {[*,*]}
 */
const bdtogcj02 = (lon, lat) => {
  let x = lon - 0.0065;
  let y = lat - 0.006;
  let z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * X_PI);
  let theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * X_PI);
  let ggLng = z * Math.cos(theta);
  let ggLat = z * Math.sin(theta);
  return [ggLng, ggLat];
};

/**
 * ç»çº¬åº¦è½¬Mercator
 * @param lontitude
 * @param latitude
 * @returns {[*,*]}
 */
const lonLatToMercator = (lontitude, latitude) => {
  let x = lontitude * 20037508.34 / 180;
  let y = Math.log(Math.tan((90 + latitude) * Math.PI / 360)) / (Math.PI / 180);
  y = y * 20037508.34 / 180;
  return [x, y];
};

/**
 * Mercatorè½¬ç»çº¬åº¦
 * @param x
 * @param y
 * @returns {[*,*]}
 * @constructor
 */
const MercatorTolonLat = (x, y) => {
  let longtitude = x / 20037508.34 * 180;
  let latitude = y / 20037508.34 * 180;
  latitude = 180 / Math.PI * (2 * Math.atan(Math.exp(latitude * Math.PI / 180)) - Math.PI / 2);
  return [longtitude, latitude];
};

export {
  transformlat,
  transformlng,
  outOfChina,
  gcj02towgs84,
  wgs84togcj02,
  gcj02tobd,
  bdtogcj02,
  lonLatToMercator,
  MercatorTolonLat
};
